import React, { FunctionComponent } from 'react'
import { AliasComponentProps } from 'Common/Types/Global';

export type ToggleProps = AliasComponentProps

const Toggle: FunctionComponent<ToggleProps> = ({as: Component, className, children, ...props}) => {
    return (
        <Component className={`navbar-toggle${className ? ` ${className}` : ''}`} {...props}>
            {children}
        </Component>
    )
}

Toggle.defaultProps = {
    as: 'a'
}

export default Toggle
