import React, { FunctionComponent } from 'react'
import { AliasComponentProps } from 'Common/Types/Global';
import Nav from './Nav';
import NavLink from './NavLink';
import Brand from './Brand';
import Toggle from './Toggle';
import './index.css';

export interface NavbarProps extends AliasComponentProps {
    fixedTop?: boolean;
    sidebar?: boolean;
}

export interface NavBarComponent<P> extends FunctionComponent<P> {
    Nav: typeof Nav;
    Link: typeof NavLink;
    Brand: typeof Brand;
    Toggle: typeof Toggle;
}

const NavBar: NavBarComponent<NavbarProps> = ({as: Component, fixedTop, sidebar, className, children}) => {
    return (
        <Component className={`navbar${fixedTop ? ' fixed-top' : ''}${sidebar ? ' sidebar' : ''}${className ? ` ${className}` : ''}`}>
            {children}
        </Component>
    )
}

NavBar.defaultProps = {
    as: 'header',
    fixedTop: false,
    sidebar: false
}

NavBar.Nav = Nav;
NavBar.Link = NavLink;
NavBar.Brand = Brand;
NavBar.Toggle = Toggle;

export default NavBar
