import React, { useState, useRef, FunctionComponent } from 'react';
import { Container, Section } from 'Template/'
import './index.css'

const Attraction: FunctionComponent = () => {
    const timeout = useRef<NodeJS.Timeout | null>(null)
    const video = useRef<HTMLVideoElement | null>(null)
    const [play, setPlay] = useState<boolean>(false)

    const handleClick = () => { 
        const tag = video.current
        if(timeout.current) clearTimeout(timeout.current)
        if(!tag) return;
        
        setPlay(!play)
        
        if(tag.paused) {
            timeout.current = setTimeout(() => {
                tag.play()
            }, 750) 
        } else {
            tag.pause()
        }
        
    }

    return (
        <Section id="attraction">
            <div className={`layer-container common-height${play ? ' active' : ''}`}>
                <div className="layer-context">
                        <Container filled>
                            <h1 className="title-1">Resort for the whole family, all year round</h1>
                            <p className="description-2">Enjoy your unique exprience of the sweeping views of the azure beach and white porcelain sand of boracay. it is perfect for romantic couple, whole family, and friend to have alot of fun!
                            </p>
                        </Container>
                </div>
                <div className="layer-cover">
                    <div className="layer-context layer-above">
                        <Container filled>
                            <h1 className="title-1">Resort for the whole family, all year round</h1>
                            <p className="description-2">Enjoy your unique exprience of the sweeping views of the azure beach and white porcelain sand of boracay. it is perfect for romantic couple, whole family, and friend to have alot of fun!
                            </p>
                        </Container>
                    </div>
                </div>
                <svg className="layer-cover-stack" viewBox="0 0 100 100" preserveAspectRatio="none" fill="#fffeec">
                    <polygon points="0,0 100,0 0,100" />
                </svg>
                <div className="layer-content">
                    <video poster="/images/beach/boracay-3.jpg" ref={video} onEnded={(e) => {
                        const { target }: any = e
                        setPlay(false);
                        target.load();
                    }}>
                        <source src="/images/beach/boracay-vid-1.mp4" type="video/mp4" />
                        Your browser does not support HTML video.
                    </video>
                    <div className="toggle" onClick={handleClick}>{play ? <i className="mytabworks icon-pause"/> : <i className="mytabworks icon-play"/>}</div>
                </div> 
            </div>
        </Section>
    )
}

Attraction.propTypes = {

}

export default Attraction
