import React, { useState, FunctionComponent } from 'react';
import { DateRangePicker } from 'react-dates';
import { Container } from 'Template/';

const Jumbotron: FunctionComponent<any> = props => {
    const [state, setState] = useState<any>({
        startDate: null,
        endDate: null,
        focusedInput: null,
    })
    return (
        <div className="landing">
            <div className="landing-bg">
                <img src="/images/beach/boracay-1.jpg" alt="boracay beach" />
            </div>
            <div className="landing-booking">
                <Container filled>
                    <div className="landing-booking-wrap"> 
                        <div className="landing-booking-container">
                            <p className="description-1">The most trusted hotel in boracay</p>
                            <h1 className="title-1">Book your best room right now</h1>  
                            <form className="landing-booking-form">
                                <div className="form-field">
                                    <label>Date:</label>
                                    <DateRangePicker
                                        showClearDates 
                                        startDateId="checkIn"
                                        endDateId="checkOut"
                                        startDatePlaceholderText="Check in"
                                        endDatePlaceholderText="Check out"
                                        startDate={state.startDate}
                                        endDate={state.endDate}
                                        customArrowIcon={<></>}
                                        customInputIcon={<i className="mytabworks icon-calendar"/>}
                                        onDatesChange={({ startDate, endDate }: any) => { setState((prev:any) => ({...prev, startDate, endDate }))}}
                                        focusedInput={state.focusedInput}
                                        onFocusChange={(focusedInput) => { setState((prev:any) => ({...prev, focusedInput }))}}
                                    />
                                </div>
                                {/* <div className="form-field">
                                    <label>Check out:</label>
                                    <input className="form-control" type="text"/>
                                </div> */}
                                <div className="form-field">
                                    <label>Guest:</label>
                                    <input className="form-control" type="number" />
                                </div>   
                            </form> 
                            <div className="form-field d-flex flex-center-x">
                                <button className="btn btn-block w-75">Check Availability</button>
                            </div>
                        </div>
                    </div> 
                </Container>
            </div>
        </div>
    )
}

Jumbotron.propTypes = {

}

export default Jumbotron
