import React, { FunctionComponent } from 'react';
import { Container, Row, Col, Section } from 'Template/'
import './index.css'

const Rooms: FunctionComponent = () => {
    return (
        <Section id="rooms">
            <Container filled>
                <h1 className="title-2">Rooms in hotel</h1>
                <p className="description-2">We offer a wide range of rooms, including single and family rooms.</p>
                <Row>
                    <Col md={6} lg={3}>
                        <div className="card">
                            <img src="/images/beds/twin-bed.jpg" alt="twin"/>
                            <h2 className="card-title">Twin bed room</h2>
                            <p><b>php 2500</b> pernight</p>
                            <a className="book-now" href="#">
                                BOOK NOW
                            </a>
                        </div>
                    </Col>
                    <Col md={6} lg={3}>
                        <div className="card">
                            <img src="/images/beds/couple.jpg" alt="couple"/>
                            <h2 className="card-title">Classic couple room</h2>
                            <p><b>php 2000</b> pernight</p>
                            <a className="book-now" href="#">
                                BOOK NOW
                            </a>
                        </div>
                    </Col>
                    <Col md={6} lg={3}>
                        <div className="card">
                            <img src="/images/beds/family.jpg" alt="family"/>
                            <h2 className="card-title">Family room</h2>
                            <p><b>php 4000</b> pernight</p>
                            <a className="book-now" href="#">
                                BOOK NOW
                            </a>
                        </div>
                    </Col>
                    <Col md={6} lg={3}>
                        <div className="card">
                            <img src="/images/beds/Suite.jpg" alt="Suite"/>
                            <h2 className="card-title">Suite rooom</h2>
                            <p><b>php 5000</b> pernight</p>
                            <a className="book-now" href="#">
                                BOOK NOW
                            </a>
                        </div>
                    </Col>
                </Row>
            </Container>
        </Section>
    )
}

Rooms.propTypes = {

}

export default Rooms
