import React, { FunctionComponent } from 'react';
import { NavBar, Container } from 'Template/';
import { Link } from 'Plugin/reaouter';

const Header : FunctionComponent<any> = () => {
    return (
        <NavBar fixedTop>
            <Container filled>
                <NavBar.Brand src="/logo192.png" alt="logo"><span>Island Princess</span></NavBar.Brand>
                
                <NavBar.Nav className="ml-auto">
                    <Link as={NavBar.Link} to='/' label='Rooms' />
                    <Link as={NavBar.Link} to='/about' label='Blogs' />
                    <NavBar.Toggle>
                        <i className="mytabworks icon-burger"/>
                    </NavBar.Toggle>
                </NavBar.Nav> 
                <NavBar.Nav collapsible>
                    <Link as={NavBar.Link} to='/about' label='Events' />
                    <Link as={NavBar.Link} to='/about' label='Contact us' />
                    <Link as={NavBar.Link} to='/about' label='Support' />
                </NavBar.Nav> 
            </Container>
        </NavBar>
    )
}

Header.propTypes = {

}

export default Header
