import React, { FunctionComponent } from 'react'
import { AliasComponentProps } from 'Common/Types/Global';

export interface BrandProps extends AliasComponentProps {
    src?: string;
    alt?: string;
}

const Brand: FunctionComponent<BrandProps> = ({as: Component, className, src, alt, children}) => {
    return (
        <Component className={`navbar-brand${className ? ` ${className}` : ''}`}>
            {src && <img className="navbar-brand-img" src={src} alt={alt} />}
            {children}
        </Component>
    )
}

Brand.defaultProps = {
    as: 'a'
}

export default Brand
