import React, { FunctionComponent } from 'react'
import { AliasComponentProps } from 'Common/Types/Global';

export type NavLinkProps = AliasComponentProps

const NavLink: FunctionComponent<NavLinkProps> = ({as: Component, className, children, ...props}) => {
    return (
        <Component className={`nav-link${className ? ` ${className}` : ''}`} {...props}>
            {children}
        </Component>
    )
}

NavLink.defaultProps = {
    as: 'a'
}

export default NavLink
