import React, { FunctionComponent } from 'react'
import { AliasComponentProps } from 'Common/Types/Global';

export interface NavProps extends AliasComponentProps {
    collapsible?: boolean;
}

const Nav: FunctionComponent<NavProps> = ({as: Component, className, collapsible, children}) => {
    return (
        <Component className={`nav${collapsible ? ' navbar-collapse' : ''}${className ? ` ${className}` : ''}`}>
            {children}
        </Component>
    )
}

Nav.defaultProps = {
    as: 'nav',
    collapsible: false
}

export default Nav
